import './New.css'
import React  from 'react';
import {Navigate} from 'react-router-dom'

class New extends React.Component{
    constructor(props){
         super(props)
        this.state = {
            title:'',
            editor:null
        }

        this.user = JSON.parse(localStorage.getItem('user'))
        this.save = this.save.bind(this)
        this.titleChanged = this.titleChanged.bind(this)
    }
    componentDidMount(){
        console.log(this.context)
        // if(!this.user) Navigate({to:'/blog'});
        const EditorJS = require('@editorjs/editorjs')
        const Header = require( '@editorjs/header'); 
        const List = require( '@editorjs/list'); 
        const editor = new EditorJS({
        /**
         * Id of Element that should contain Editor instance
         */
        holder: 'editor',
        tools: { 
            header: Header, 
            list: List 
        }, 
        })
        this.setState({
            editor
        })        

    }
    
    async save (){
        const data =  await this.state.editor.save()
        data.title = this.state.title
        data.project = 'none'
        data.author = this.user._id
        data.public = false
        fetch('/api/blog/new',{
            method: 'POST',
            headers: {
              'auth-token':this.user['auth-token'],
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body:JSON.stringify(data)
        })

        console.log( data)
      }
      titleChanged (e){
        this.setState({title:e.target.value})
      }
      render(){
        return (    
            <article>
                { !this.user && <Navigate to={'/blog'}></Navigate>}
                <fieldset>
                    <label>Title</label>
                    <input type='text' name='title' id='titleInput' value={this.state.title} onChange={this.titleChanged}></input>
                </fieldset>
                <div id='editor'></div>
                <button onClick={this.save}>SAVE</button>
            </article>
        )
      }
    
}

export default New;