import { useEffect, useState } from 'react';
import {useParams}  from "react-router-dom"
import PostHeader from './PostHeader';

import './Post.css'
    
    function Post(props){

        const id = useParams().id 
        const [data, setData] = useState({
            "author":{
                "name":"aelmardhi",
                "profileImage":"https://res.cloudinary.com/dzrwq9pyu/image/upload/v1589248125/profile_images/5eb58733e838f7001761adfb.jpg"
            },
            "project":"none",
            "time": 1656228357017,
            "blocks": [
                {
                    "id": "UG4z_fkBvm",
                    "type": "paragraph",
                    "data": {
                        "text": "I'm exited to announce that I'm working on a new <b>Blog</b>&nbsp;to talk about my projects and technical reviews . I am very passionate about web technologies specially the mern stack wich I'm using to build this blog"
                    }
                },
                {
                    "id": "TvKk4mepcH",
                    "type": "header",
                    "data": {
                        "text": "MERN Stack Components",
                        "level": 2
                    }
                },
                {
                    "id": "iN1_6gZ3-Y",
                    "type": "list",
                    "data": {
                        "style": "ordered",
                        "items": [
                            "NodeJs",
                            "ExpressJs",
                            "ReactJs",
                            "MongoDb"
                        ]
                    }
                }
            ],
            "version": "2.25.0",
            "title": "new Blog"
        })

        useEffect(()=>{

            fetch('/api/blog/post/'+id,{
                method: 'GET',
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
        }).then(r=>r.json().then(d=>{
            console.log(d)
             setData(d)
        }),[])
    
        },[])

        return (
            <article className='post'>
                <PostHeader post={data}></PostHeader>
                <div className='postContent'>
                    {data.blocks.map(b=>{
                        switch(b.type){
                            case 'paragraph':
                            return (<p dangerouslySetInnerHTML={{__html:b.data.text}}></p>)
                            
                            case 'list':
                                if(b.data.style === 'ordered'){
                                    return(<ol>{b.data.items.map(i=>{
                                        return(<li>{i}</li>)
                                    })}</ol>)
                                }
                                return(<ul>{b.data.items.map(i=>{
                                        return(<li>{i}</li>)
                                    })}</ul>)
                                
                                case 'header':
                                    switch(b.data.level){
                                        case 1:
                                            return(<h1>{b.data.text}</h1>)
                                            
                                        case 2:
                                            return(<h2>{b.data.text}</h2>)
                                            
                                        case 3:
                                            return(<h3>{b.data.text}</h3>)
                                            
                                        case 4:
                                            return(<h4>{b.data.text}</h4>)
                                            
                                        case 5:
                                            return(<h5>{b.data.text}</h5>)
                                            
                                        case 6:
                                            return(<h6>{b.data.text}</h6>)
                                            
                                        default:
                                            return(<h3>{b.data.text}</h3>)
                                            
                                    }
                                    
                                default:
                                    return(<br></br>)
                        }
                    })}
                </div>
            </article>
        )
    }


export default Post